import React, {useState, useEffect, useCallback} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import NativeSelect from '@material-ui/core/NativeSelect';
import DialogActions from '@material-ui/core/DialogActions';
import Snackbar from '@material-ui/core/Snackbar';
import Alert from '@material-ui/lab/Alert';
import Container from '@material-ui/core/Container';
import Tooltip from '@material-ui/core/Tooltip';
import AddIcon from '@material-ui/icons/Add';
import TableContainer from '@material-ui/core/TableContainer';
import Table from '@material-ui/core/Table';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell';
import TableBody from '@material-ui/core/TableBody';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import DoneIcon from '@material-ui/icons/Done';
import CancelIcon from '@material-ui/icons/Cancel';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import userStore from '../stores/UserStore';
import axios from 'axios';
import UserAvatar from '../components/UserAvatar';
import SideMenu from '../components/SideMenu';
import SearchIcon from '@material-ui/icons/Search';
import FileCopyIcon from '@material-ui/icons/FileCopy';
import MonthOptions from '../components/MonthOptions';


const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
        '& .MuiTypography-h5': {
            fontWeight: 600
        },
        '& .MuiTypography-h6': {
            fontWeight: 600
        }
    },
    title: {
        flexGrow: 1
    },
    container: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
    },
    tableToolbar: {
        '& .MuiFormControl-root': {
            marginRight: theme.spacing(2),
            marginLeft: theme.spacing(1),
            width: 100
        },
        marginBottom: theme.spacing(1)
    },
    table: {
        '& .MuiTableCell-root': {
            whiteSpace: 'nowrap',
            '& .MuiTextField-root': {
                width: 100
            }
        },
        '& .MuiTableHead-root': {
            '& .MuiTableRow-root': {
                '& .MuiTableCell-root': {
                    fontWeight: 600
                }
            }
        }
    },
    dialogTitle: {
        '& .MuiTypography-h6': {
            fontWeight: 600
        }
    }
}));

export default function FixedTime() {
    const classes = useStyles();
    const [selectedYear, setSelectedYear] = useState(new Date().getFullYear());
    const [selectedMonth, setSelectedMonth] = useState(new Date().getMonth() + 1);
    const [rows, setRows] = useState([]);
    const [oldRows, setOldRows] = useState({});
    const [addModalOpen, setAddModelOpen] = useState(false);
    const [year, setYear] = useState(selectedYear);
    const [month, setMonth] = useState(selectedMonth);
    const [customer, setCustomer] = useState('');
    const [startTime, setStartTime] = useState('');
    const [endTime, setEndTime] = useState('');
    const [severity, setSeverity] = useState('error');
    const [message, setMessage] = useState('');
    const [snackbarOpen, setSnackbarOpen] = useState(false);

    /**
     * 所定時間リストを取得します。
     */
    const loadFixedTimes = useCallback(() => {
        axios.get(`${process.env.REACT_APP_REST_URI}/fixed_times/${selectedYear}/${selectedMonth}`, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then((response) => {
            const records = response.data['records'];
            const rows = records.map(record => {
                return {
                    edit: false,
                    id: record['fixed_time_id'],
                    year: record['year'],
                    month: record['month'],
                    customer: record['customer'],
                    start_time: record['start_time'],
                    end_time: record['end_time']
                };
            });
            setRows(rows);
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    }, [selectedYear, selectedMonth]);

    /**
     * 画面ロード時に呼ばれます。
     */
    useEffect(() => {
        if (selectedYear > 0) {
            loadFixedTimes();
        }
    }, [selectedYear, selectedMonth, loadFixedTimes]);

    /**
     * 検索ボタンがクリックされた場合に呼ばれます。
     */
    const handleClickSearch = () => {
        // 年の入力がない場合はエラー
        if (!selectedYear) {
            setSeverity('error');
            setMessage('年が入力されていません。');
            setSnackbarOpen(true);
            return;
        }

        loadFixedTimes();
    };

    /**
     * 追加ボタンがクリックされた場合に呼ばれます。
     */
    const handleClickAdd = () => {
        setYear(selectedYear);
        setMonth(selectedMonth);
        setCustomer('');
        setStartTime('');
        setEndTime('');
        setAddModelOpen(true);
    };

    /**
     * 前月流用ボタンがクリックされた場合に呼ばれます。
     */
    const handleClickCopy = () => {
        // 年の入力がない場合はエラー
        if (!selectedYear) {
            setSeverity('error');
            setMessage('年が入力されていません。');
            setSnackbarOpen(true);
            return;
        }

        axios.post(`${process.env.REACT_APP_REST_URI}/fixed_times/copy`, {
            from_year: 1 === Number(selectedMonth) ? Number(selectedYear) - 1 : Number(selectedYear),
            from_month: 1 === Number(selectedMonth) ? 12 : Number(selectedMonth) - 1,
            to_year: Number(selectedYear),
            to_month: Number(selectedMonth)
        },{
            params: {
                token: userStore.getAccessToken()
            }
        }).then((response) => {
            const records = response.data['records'];
            const rows = records.map(record => {
                return {
                    edit: false,
                    id: record['fixed_time_id'],
                    year: record['year'],
                    month: record['month'],
                    customer: record['customer'],
                    start_time: record['start_time'],
                    end_time: record['end_time']
                };
            });
            setRows(rows);
            setSeverity('success');
            setMessage('前月の設定をコピーしました。');
            setSnackbarOpen(true);
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    };

    /**
     * 所定時間追加ダイアログでキャンセルボタンがクリックされた場合に呼ばれます。
     */
    const handleClickDialogCancel = () => {
        setAddModelOpen(false);
    };

    /**
     * 所定時間追加ダイアログで追加ボタンがクリックされた場合に呼ばれます。
     */
    const handleClickDialogAdd = () => {
        if (!customer) {
            setSeverity('error');
            setMessage('客先が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        if (!startTime) {
            setSeverity('error');
            setMessage('開始時間が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        if (!endTime) {
            setSeverity('error');
            setMessage('終了時間が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        const regex = RegExp(/\d{1,2}:\d{1,2}/);
        if (!regex.test(startTime)) {
            setSeverity('error');
            setMessage('開始時間は「HH:mm」形式で入力してください。');
            setSnackbarOpen(true);
            return;
        }
        if (!regex.test(endTime)) {
            setSeverity('error');
            setMessage('終了時間は「HH:mm」形式で入力してください。');
            setSnackbarOpen(true);
            return;
        }
        if (!validationTime(startTime, endTime)) {
            setSeverity('error');
            setMessage('開始時間は終了時間よりも前の時間を入力してください。');
            setSnackbarOpen(true);
            return;
        }

        axios.post(`${process.env.REACT_APP_REST_URI}/fixed_times`, {
            year: year,
            month: month,
            customer: customer,
            start_time: startTime,
            end_time: endTime
        }, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then((response) => {
            const record = response.data['record'];
            rows.push({
                edit: false,
                id: record['fixed_time_id'],
                year: record['year'],
                month: record['month'],
                customer: record['customer'],
                start_time: record['start_time'],
                end_time: record['end_time']
            });
            setRows(rows.slice());
            setAddModelOpen(false);
            setSeverity('success');
            setMessage('所定時間を追加しました。');
            setSnackbarOpen(true);
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    };

    /**
     * 確定ボタンがクリックされた場合に呼ばれます。
     *
     * @param row 所定時間情報
     */
    const handleClickDone = (row) => {
        if (!row.customer) {
            setSeverity('error');
            setMessage('客先が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        if (!row.start_time) {
            setSeverity('error');
            setMessage('開始時間が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        if (!row.end_time) {
            setSeverity('error');
            setMessage('終了時間が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        const regex = RegExp(/\d{1,2}:\d{1,2}/);
        if (!regex.test(row.start_time)) {
            setSeverity('error');
            setMessage('開始時間は「HH:mm」形式で入力してください。');
            setSnackbarOpen(true);
            return;
        }
        if (!regex.test(row.end_time)) {
            setSeverity('error');
            setMessage('終了時間は「HH:mm」形式で入力してください。');
            setSnackbarOpen(true);
            return;
        }
        if (!validationTime(row.start_time, row.end_time)) {
            setSeverity('error');
            setMessage('開始時間は終了時間よりも前の時間を入力してください。');
            setSnackbarOpen(true);
            return;
        }

        axios.put(`${process.env.REACT_APP_REST_URI}/fixed_times/${row.id}`, {
            year: row.year,
            month: row.month,
            customer: row.customer,
            start_time: row.start_time,
            end_time: row.end_time
        }, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then((response) => {
            const record = response.data['record'];
            row.edit = false;
            row.year = record['year'];
            row.month = record['month'];
            row.customer = record['customer'];
            row.start_time = record['start_time'];
            row.end_time = record['end_time'];
            setRows(rows.slice());
            setSeverity('success');
            setMessage('更新しました。');
            setSnackbarOpen(true);
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    };

    /**
     * キャンセルボタンがクリックされた場合に呼ばれます。
     *
     * @param row 所定時間情報
     */
    const handleClickCancel = (row) => {
        const oldRow = oldRows[String(row.id)];
        if (oldRow) {
            row.customer = oldRow.customer;
            row.start_time = oldRow.start_time;
            row.end_time = oldRow.end_time;
            delete oldRows[String(row.id)];
            setOldRows(oldRows);
        }
        row.edit = false;
        setRows(rows.slice());
    };

    /**
     * 編集ボタンがクリックされた場合に呼ばれます。
     *
     * @param row 所定時間情報
     */
    const handleClickEdit = (row) => {
        delete oldRows[String(row.id)];
        setOldRows(oldRows);
        row.edit = true;
        setRows(rows.slice());
    };

    /**
     * 削除ボタンがクリックされた場合に呼ばれます。
     *
     * @param row 所定時間情報
     */
    const handleClickDelete = (row) => {
        axios.delete(`${process.env.REACT_APP_REST_URI}/fixed_times/${row.id}`, {
            params: {
                token: userStore.getAccessToken()
            }
        }).then(() => {
            delete rows[rows.indexOf(row)];
            setRows(rows.slice());
            setSeverity('success');
            setMessage('削除しました。');
            setSnackbarOpen(true);
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    };

    /**
     * 客先が変更された場合に呼ばれます。
     *
     * @param row 所定時間情報
     * @param customer 客先
     */
    const handleChangeCustomer = (row, customer) => {
        backupRow(row);
        row.customer = customer;
        setRows(rows.slice());
    };

    /**
     * 開始時間が変更された場合に呼ばれます。
     *
     * @param row 所定時間情報
     * @param start_time 開始時間
     */
    const handleChangeStartTime = (row, start_time) => {
        backupRow(row);
        row.start_time = start_time;
        setRows(rows.slice());
    };

    /**
     * 終了時間が変更された場合に呼ばれます。
     *
     * @param row 所定時間情報
     * @param end_time 終了時間
     */
    const handleChangeEndTime = (row, end_time) => {
        backupRow(row);
        row.end_time = end_time;
        setRows(rows.slice());
    };

    /**
     * 所定時間情報をバックアップします。
     *
     * @param row 所定時間情報
     */
    const backupRow = (row) => {
        if (!oldRows.hasOwnProperty(String(row.id))) {
            oldRows[String(row.id)] = Object.assign({}, row);
        }
    };

    /**
     * スナックバーの閉じるイベント時に呼ばれます。
     *
     * @param event イベント
     * @param reason 理由
     */
    const handleCloseSnackbar = (event, reason) => {
        if ('clickaway' === reason) {
            return;
        }
        setSnackbarOpen(false);
    };

    /**
     * 時間の前後検証を実施します。
     *
     * @param start 開始時間
     * @param end 終了時間
     * @returns {boolean} true: 検証OK, false: 検証NG
     */
    const validationTime = (start, end) => {
        const now = new Date();
        const starts = start.split(':');
        const ends = end.split(':');
        const sdt = new Date(now.getFullYear(), now.getMonth(), now.getDate(), Number(starts[0]), Number(starts[1]));
        const edt = new Date(now.getFullYear(), now.getMonth(), now.getDate(), Number(ends[0]), Number(ends[1]));
        return sdt < edt;
    };

    return (
        <form className={classes.root}>
            <CssBaseline />

            <AppBar position='sticky'>
                <Toolbar>
                    <SideMenu/>
                    <Typography variant='h6' className={classes.title}>
                        株式会社ベイクラフト
                    </Typography>
                    <UserAvatar/>
                </Toolbar>
            </AppBar>

            <Dialog open={addModalOpen} onClose={() => setAddModelOpen(false)}>
                <DialogTitle className={classes.dialogTitle}>所定時間追加</DialogTitle>
                <DialogContent>
                    <Grid container={true} spacing={2}>
                        <Grid item={true} xs={3}>
                            <TextField label="年" fullWidth={true} required={true} margin="dense"
                                       defaultValue={year} disabled={true} />
                        </Grid>
                        <Grid item={true} xs={3}>
                            <TextField label="月" fullWidth={true} required={true} margin="dense"
                                       defaultValue={month} disabled={true} />
                        </Grid>
                        <Grid item={true} xs={6} />
                        <Grid item={true} xs={4}>
                            <TextField autoFocus={true} label="客先" fullWidth={true} required={true} margin="dense"
                                       onChange={(event) => setCustomer(event.target.value)} />
                        </Grid>
                        <Grid item={true} xs={4}>
                            <TextField label="開始時間" fullWidth={true} required={true} margin="dense"
                                       onChange={(event) => setStartTime(event.target.value)} />
                        </Grid>
                        <Grid item={true} xs={4}>
                            <TextField label="終了時間" fullWidth={true} required={true} margin="dense"
                                       onChange={(event) => setEndTime(event.target.value)} />
                        </Grid>
                    </Grid>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClickDialogCancel} color="primary">
                        キャンセル
                    </Button>
                    <Button onClick={handleClickDialogAdd} color="primary">
                        追加
                    </Button>
                </DialogActions>
            </Dialog>

            <Snackbar open={snackbarOpen} autoHideDuration={6000} onClose={handleCloseSnackbar}>
                <Alert severity={severity} variant="filled" onClose={handleCloseSnackbar}>{message}</Alert>
            </Snackbar>

            <Container maxWidth='lg' className={classes.container}>
                <Toolbar className={classes.tableToolbar}>
                    <Typography variant="h5" className={classes.title}>
                        所定時間設定
                    </Typography>
                    <TextField required={true} label="年" margin="normal" defaultValue={selectedYear}
                               onChange={(event) => setSelectedYear(event.target.value)} />
                    <FormControl required={true} margin="normal">
                        <InputLabel htmlFor="select-month">月</InputLabel>
                        <NativeSelect value={selectedMonth} inputProps={{id: 'select-month'}}
                                      onChange={(event) => setSelectedMonth(event.target.value)}>
                            <MonthOptions />
                        </NativeSelect>
                    </FormControl>
                    <Tooltip title="検索">
                        <IconButton onClick={handleClickSearch}>
                            <SearchIcon />
                        </IconButton>
                    </Tooltip>
                    <Tooltip title="追加">
                        <IconButton onClick={handleClickAdd}>
                            <AddIcon />
                        </IconButton>
                    </Tooltip>
                    <Tooltip title="前月流用">
                        <IconButton onClick={handleClickCopy}>
                            <FileCopyIcon />
                        </IconButton>
                    </Tooltip>
                </Toolbar>
                <TableContainer component={Paper}>
                    <Table size="small" className={classes.table} aria-label="breaktime">
                        <TableHead>
                            <TableRow>
                                <TableCell align="center">年</TableCell>
                                <TableCell align="center">月</TableCell>
                                <TableCell align="center">客先</TableCell>
                                <TableCell align="center">開始時間</TableCell>
                                <TableCell align="center">終了時間</TableCell>
                                <TableCell align="center">操作</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows.map(row => (
                                <TableRow key={row.id}>
                                    <TableCell align="center"><span>{row.year}</span></TableCell>
                                    <TableCell align="center"><span>{row.month}</span></TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <TextField defaultValue={row.customer}
                                                       inputProps={{style: {textAlign: 'center'}}}
                                                       autoFocus={true}
                                                       onChange={(event) => handleChangeCustomer(row, event.target.value)} />
                                        ) : (
                                            <span>{row.customer}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <TextField defaultValue={row.start_time}
                                                       inputProps={{style: {textAlign: 'center'}}}
                                                       onChange={(event) => handleChangeStartTime(row, event.target.value)} />
                                        ) : (
                                            <span>{row.start_time}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <TextField defaultValue={row.end_time}
                                                       inputProps={{style: {textAlign: 'center'}}}
                                                       onChange={(event) => handleChangeEndTime(row, event.target.value)} />
                                        ) : (
                                            <span>{row.end_time}</span>
                                        )}
                                    </TableCell>
                                    <TableCell align="center">
                                        {row.edit ? (
                                            <div>
                                                <IconButton aria-label="done" onClick={() => handleClickDone(row)}>
                                                    <DoneIcon />
                                                </IconButton>
                                                <IconButton aria-label="cancel" onClick={() => handleClickCancel(row)}>
                                                    <CancelIcon />
                                                </IconButton>
                                            </div>
                                        ) : (
                                            <div>
                                                <IconButton aria-label="edit" onClick={() => handleClickEdit(row)}>
                                                    <EditIcon />
                                                </IconButton>
                                                <IconButton aria-label="delete" onClick={() => handleClickDelete(row)}>
                                                    <DeleteIcon />
                                                </IconButton>
                                            </div>
                                        )}
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Container>
        </form>
    );
}