import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Toolbar from '@material-ui/core/Toolbar';
import SideMenu from '../components/SideMenu';
import Typography from '@material-ui/core/Typography';
import UserAvatar from '../components/UserAvatar';
import AppBar from '@material-ui/core/AppBar';
import Container from '@material-ui/core/Container';
import Paper from '@material-ui/core/Paper';
import TableContainer from '@material-ui/core/TableContainer';
import Table from '@material-ui/core/Table';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import Tooltip from '@material-ui/core/Tooltip';
import Alert from '@material-ui/lab/Alert';
import Snackbar from '@material-ui/core/Snackbar';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import NativeSelect from '@material-ui/core/NativeSelect';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
        '& .MuiTypography-h5': {
            fontWeight: 600
        },
        '& .MuiTypography-h6': {
            fontWeight: 600
        }
    },
    title: {
        flexGrow: 1
    },
    container: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
    },
    tableToolbar: {
        '& .MuiFormControl-root': {
            marginRight: theme.spacing(2),
            marginLeft: theme.spacing(1),
            width: 100
        },
        marginBottom: theme.spacing(1)
    },
    tableContainer: {
        marginBottom: theme.spacing(3)
    },
    table: {
        '& .MuiTableCell-root': {
            whiteSpace: 'nowrap',
            '& .MuiTextField-root': {
                width: 100
            }
        },
        '& .MuiTableHead-root': {
            '& .MuiTableRow-root': {
                '& .MuiTableCell-root': {
                    fontWeight: 600
                }
            }
        }
    },
    dialogTitle: {
        '& .MuiTypography-h6': {
            fontWeight: 600
        }
    }
}));

export default function Expense() {
    const classes = useStyles();
    const [severity, setSeverity] = useState('error');
    const [message, setMessage] = useState('');
    const [snackbarOpen, setSnackbarOpen] = useState(false);
    const [addTransportOpen, setAddTransportOpen] = useState(false);
    const [year, setYear] = useState(new Date().getFullYear());
    const [month, setMonth] = useState(new Date().getMonth() + 1);
    const [date, setDate] = useState(new Date().getDate());
    const [customer, setCustomer] = useState('');
    const [journal, setJournal] = useState(1);
    const [breakdown, setBreakdown] = useState(1);
    const [billTo, setBillTo] = useState(1);
    const [cost, setCost] = useState(0);
    const [transport, setTransport] = useState('');
    const [departure, setDeparture] = useState('');
    const [arrival, setArrival] = useState('');

    /**
     * 日の選択肢を描画するコンポーネントです。
     *
     * @returns {[]} 選択肢リスト
     * @constructor
     */
    const DateOptions = () => {
        const options = [];
        const now = new Date();
        const work = new Date(now.getFullYear(), now.getMonth(), 1);
        while (true) {
            const date = work.getDate();
            options.push(<option key={`${work.getFullYear()}-${work.getMonth()}-${date}`}>{date}</option>);
            work.setDate(date + 1);
            if (now.getMonth() !== work.getMonth()) {
                break;
            }
        }
        return options;
    };

    /**
     * スナックバーの閉じるイベント時に呼ばれます。
     *
     * @param event イベント
     * @param reason 理由
     */
    const handleCloseSnackbar = (event, reason) => {
        if ('clickaway' === reason) {
            return;
        }
        setSnackbarOpen(false);
    };

    /**
     * 交通費の追加ボタンがクリックされた場合に呼ばれます。
     */
    const handleClickAddTransport = () => {
        const now = new Date();
        setYear(String(now.getFullYear()));
        setMonth(String(now.getMonth() + 1));
        setDate(String(now.getDate()));
        setCustomer('');
        setJournal(1);
        setBreakdown(1);
        setBillTo(1);
        setCost(0);
        setTransport('');
        setDeparture('');
        setArrival('');
        setAddTransportOpen(true);
    };

    /**
     * 交通費金額の値が変更された場合に呼ばれます。
     *
     * @param event イベント
     */
    const handleChangeCost = (event) => {
        if (/^([1-9]\d*|0)$/.test(event.target.value)) {
            setCost(Number(event.target.value));
        }
        else {
            event.target.value = '';
        }
    };

    /**
     * 交通費追加ダイアログの追加ボタンがクリックされた場合に呼ばれます。
     */
    const handleClickDialogAddTransport = () => {
        if (!customer) {
            setSeverity('error');
            setMessage('客先が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        if (!cost) {
            setSeverity('error');
            setMessage('金額が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        if (!transport) {
            setSeverity('error');
            setMessage('交通機関が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        if (!departure) {
            setSeverity('error');
            setMessage('出発経路が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
        if (!arrival) {
            setSeverity('error');
            setMessage('到着経路が入力されていません。');
            setSnackbarOpen(true);
            return;
        }
    };

    return (
        <form className={classes.root}>
            <CssBaseline/>

            <AppBar position='sticky'>
                <Toolbar>
                    <SideMenu/>
                    <Typography variant='h6' className={classes.title}>
                        株式会社ベイクラフト
                    </Typography>
                    <UserAvatar/>
                </Toolbar>
            </AppBar>

            <Snackbar open={snackbarOpen} autoHideDuration={6000} onClose={handleCloseSnackbar}>
                <Alert severity={severity} variant="filled" onClose={handleCloseSnackbar}>{message}</Alert>
            </Snackbar>

            <Dialog open={addTransportOpen} onClose={() => setAddTransportOpen(false)}>
                <DialogTitle className={classes.dialogTitle}>交通費追加</DialogTitle>
                <DialogContent>
                    <Grid container={true} spacing={2}>
                        <Grid item={true} xs={4}>
                            <TextField label="年" fullWidth={true} required={true} margin="dense"
                                       defaultValue={year} disabled={true} />
                        </Grid>
                        <Grid item={true} xs={4}>
                            <TextField label="月" fullWidth={true} required={true} margin="dense"
                                       defaultValue={month} disabled={true} />
                        </Grid>
                        <Grid item={true} xs={4}>
                            <FormControl required={true} fullWidth={true} margin="dense">
                                <InputLabel htmlFor="select-date">日</InputLabel>
                                <NativeSelect value={date} inputProps={{id: 'select-date'}}
                                              onChange={(event) => setDate(event.target.value)}>
                                    <DateOptions />
                                </NativeSelect>
                            </FormControl>
                        </Grid>
                        <Grid item={true} xs={12}>
                            <TextField autoFocus={true} label="客先" fullWidth={true} required={true} margin="dense"
                                       onChange={(event) => setCustomer(event.target.value)} />
                        </Grid>
                        <Grid item={true} xs={4}>
                            <FormControl required={true} fullWidth={true} margin="dense">
                                <InputLabel htmlFor="select-journal">仕訳</InputLabel>
                                <NativeSelect value={journal} inputProps={{id: 'select-journal'}}
                                              onChange={(event) => setJournal(Number(event.target.value))}>
                                    <option value={10}>通勤</option>
                                    <option value={11}>出張</option>
                                </NativeSelect>
                            </FormControl>
                        </Grid>
                        <Grid item={true} xs={4}>
                            <FormControl required={true} fullWidth={true} margin="dense">
                                <InputLabel htmlFor="select-breakdown">内訳</InputLabel>
                                <NativeSelect value={breakdown} inputProps={{id: 'select-breakdown'}}
                                              onChange={(event) => setBreakdown(Number(event.target.value))}>
                                    <option value={10}>定期</option>
                                    <option value={11}>切符</option>
                                    <option value={12}>ICカード</option>
                                    <option value={13}>現金</option>
                                </NativeSelect>
                            </FormControl>
                        </Grid>
                        <Grid item={true} xs={4}>
                            <FormControl required={true} fullWidth={true} margin="dense">
                                <InputLabel htmlFor="select-bill-to">請求先</InputLabel>
                                <NativeSelect value={billTo} inputProps={{id: 'select-bill-to'}}
                                              onChange={(event) => setBillTo(Number(event.target.value))}>
                                    <option value={1}>社内</option>
                                    <option value={2}>社外</option>
                                </NativeSelect>
                            </FormControl>
                        </Grid>
                        <Grid item={true} xs={12}>
                            <TextField label="金額" fullWidth={true} required={true} margin="dense"
                                       onChange={(event) => handleChangeCost(event)} />
                        </Grid>
                        <Grid item={true} xs={4}>
                            <TextField label="交通機関" fullWidth={true} required={true} margin="dense"
                                       onChange={(event) => setTransport(event.target.value)} />
                        </Grid>
                        <Grid item={true} xs={4}>
                            <TextField label="出発経路" fullWidth={true} required={true} margin="dense"
                                       onChange={(event) => setDeparture(event.target.value)} />
                        </Grid>
                        <Grid item={true} xs={4}>
                            <TextField label="到着経路" fullWidth={true} required={true} margin="dense"
                                       onChange={(event) => setArrival(event.target.value)} />
                        </Grid>
                    </Grid>
                </DialogContent>
                <DialogActions>
                    <Button color="primary" onClick={() => setAddTransportOpen(false)}>
                        キャンセル
                    </Button>
                    <Button color="primary" onClick={handleClickDialogAddTransport}>
                        追加
                    </Button>
                </DialogActions>
            </Dialog>

            <Container maxWidth='lg' className={classes.container}>
                <Toolbar className={classes.tableToolbar}>
                    <Typography variant="h5" className={classes.title}>
                        経費記録
                    </Typography>
                </Toolbar>
                <TableContainer component={Paper} className={classes.tableContainer}>
                    <Toolbar>
                        <Typography variant="h6" className={classes.title}>交通費</Typography>
                        <Tooltip title="追加">
                            <IconButton onClick={handleClickAddTransport}>
                                <AddIcon />
                            </IconButton>
                        </Tooltip>
                    </Toolbar>
                    <Table size="small" className={classes.table} aria-label="expenses">
                        <TableHead>
                            <TableRow>
                                <TableCell align="center">日付</TableCell>
                                <TableCell align="center">客先</TableCell>
                                <TableCell align="center">仕訳</TableCell>
                                <TableCell align="center">内訳</TableCell>
                                <TableCell align="center">金額</TableCell>
                                <TableCell align="center">請求先</TableCell>
                                <TableCell align="center">交通機関</TableCell>
                                <TableCell align="center">出発経路</TableCell>
                                <TableCell align="center">到着経路</TableCell>
                                <TableCell align="center">操作</TableCell>
                            </TableRow>
                        </TableHead>
                    </Table>
                </TableContainer>
                <TableContainer component={Paper} className={classes.tableContainer}>
                    <Toolbar>
                        <Typography variant="h6" className={classes.title}>経費</Typography>
                        <Tooltip title="追加">
                            <IconButton>
                                <AddIcon />
                            </IconButton>
                        </Tooltip>
                    </Toolbar>
                    <Table size="small" className={classes.table} aria-label="expenses">
                        <TableHead>
                            <TableRow>
                                <TableCell align="center">日付</TableCell>
                                <TableCell align="center">客先</TableCell>
                                <TableCell align="center">金額</TableCell>
                                <TableCell align="center">請求先</TableCell>
                                <TableCell align="center">領収書</TableCell>
                                <TableCell align="center">購入物品</TableCell>
                                <TableCell align="center">操作</TableCell>
                            </TableRow>
                        </TableHead>
                    </Table>
                </TableContainer>
            </Container>
        </form>
    );
}