import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import MenuIcon from '@material-ui/icons/Menu';
import IconButton from '@material-ui/core/IconButton';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import {Link} from 'react-router-dom';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import AccessTimeIcon from '@material-ui/icons/AccessTime';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import TimelapseIcon from '@material-ui/icons/Timelapse';
import TimerOffIcon from '@material-ui/icons/TimerOff';
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import userStore from '../stores/UserStore';
import Collapse from '@material-ui/core/Collapse';
import BallotIcon from '@material-ui/icons/Ballot';
import PeopleIcon from '@material-ui/icons/People';
import AttachFileIcon from '@material-ui/icons/AttachFile';


const useStyles = makeStyles(theme => ({
    menuButton: {
        marginRight: theme.spacing(2)
    },
    drawerList: {
        width: 250
    },
    nested: {
        paddingLeft: theme.spacing(4),
    }
}));

export default function SideMenu() {
    const classes = useStyles();
    const [drawerOpen, setDrawerOpen] = useState(false);
    const [user] = useState(userStore.getUser());
    const [adminOpen, setAdminOpen] = useState(false);

    const AdminMenu = () => {
        if (user['is_admin']) {
            return (
                <React.Fragment>
                    <Divider />
                    <ListItem button onClick={() => setAdminOpen(!adminOpen)}>
                        <ListItemIcon>
                            <BallotIcon/>
                        </ListItemIcon>
                        <ListItemText primary="管理" />
                        {adminOpen ? <ExpandLess /> : <ExpandMore />}
                    </ListItem>
                    <Collapse in={adminOpen} timeout="auto" unmountOnExit>
                        <List component="div" disablePadding>
                            <ListItem button className={classes.nested}>
                                <ListItemIcon>
                                    <PeopleIcon/>
                                </ListItemIcon>
                                <ListItemText primary="ユーザ一覧" />
                            </ListItem>
                        </List>
                    </Collapse>
                </React.Fragment>
            );
        }
        else {
            return (
                <React.Fragment>
                </React.Fragment>
            )
        }
    };

    return (
        <div>
            <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu" onClick={() => setDrawerOpen(true)}>
                <MenuIcon />
            </IconButton>
            <Drawer open={drawerOpen} onClose={() => setDrawerOpen(false)}>
                <div className={classes.drawerList}>
                    <List component="nav">
                        <ListItem button component={Link} to="/timerecord">
                            <ListItemIcon><AccessTimeIcon/></ListItemIcon>
                            <ListItemText primary="勤怠記録"/>
                        </ListItem>
                        <ListItem button component={Link} to="/expense">
                            <ListItemIcon><AttachMoneyIcon/></ListItemIcon>
                            <ListItemText primary="経費記録"/>
                        </ListItem>
                        <Divider />
                        <ListItem button component={Link} to="/fixedtime">
                            <ListItemIcon><TimelapseIcon/></ListItemIcon>
                            <ListItemText primary="所定時間設定"/>
                        </ListItem>
                        <ListItem button component={Link} to="/breaktime">
                            <ListItemIcon><TimerOffIcon/></ListItemIcon>
                            <ListItemText primary="休憩時間設定"/>
                        </ListItem>
                        <Divider />
                        <ListItem button component={Link} to="/documents">
                            <ListItemIcon><AttachFileIcon/></ListItemIcon>
                            <ListItemText primary="各種ドキュメント"/>
                        </ListItem>
                        <AdminMenu/>
                    </List>
                </div>
            </Drawer>
        </div>
    );
}
