import React, {useState} from 'react';
import Toolbar from "@material-ui/core/Toolbar";
import SideMenu from "../components/SideMenu";
import Typography from "@material-ui/core/Typography";
import UserAvatar from "../components/UserAvatar";
import AppBar from "@material-ui/core/AppBar";
import {makeStyles} from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import axios from "axios";
import userStore from "../stores/UserStore";
import {saveAs} from "file-saver";
import Snackbar from "@material-ui/core/Snackbar";
import Alert from "@material-ui/lab/Alert";
import pdf from '../images/pdf.svg';
import Button from "@material-ui/core/Button";

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
        '& .MuiTypography-h5': {
            fontWeight: 600
        },
        '& .MuiTypography-h6': {
            fontWeight: 600
        }
    },
    title: {
        flexGrow: 1
    },
    container: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
    },
    icon: {
        width: 32,
        height: 32,
        marginRight: theme.spacing(2)
    }
}));

export default function Documents() {
    const classes = useStyles();
    const [severity, setSeverity] = useState('error');
    const [message, setMessage] = useState('');
    const [snackbarOpen, setSnackbarOpen] = useState(false);

    /**
     * スナックバーの閉じるイベント時に呼ばれます。
     *
     * @param event イベント
     * @param reason 理由
     */
    const handleCloseSnackbar = (event, reason) => {
        if ('clickaway' === reason) {
            return;
        }
        setSnackbarOpen(false);
    };

    /**
     * 指定されたファイルをダウンロードします。
     *
     * @param fileName ファイル名
     */
    const handleDownload = (fileName) => {
        axios.get(`${process.env.REACT_APP_REST_URI}/docs/${fileName}`, {
            params: {
                token: userStore.getAccessToken()
            },
            responseType: 'blob'
        }).then((response) => {
            const blob = new Blob([response.data], {
                type: response.data.type
            });
            saveAs(blob, fileName);
        }).catch((error) => {
            setSeverity('error');
            setMessage(error['message']);
            setSnackbarOpen(true);
        });
    };

    return (
        <div>
            <AppBar position='sticky'>
                <Toolbar>
                    <SideMenu/>
                    <Typography variant='h6' className={classes.title}>
                        株式会社ベイクラフト
                    </Typography>
                    <UserAvatar/>
                </Toolbar>
            </AppBar>

            <Snackbar open={snackbarOpen} autoHideDuration={6000} onClose={handleCloseSnackbar}>
                <Alert severity={severity} variant="filled" onClose={handleCloseSnackbar}>{message}</Alert>
            </Snackbar>

            <Container maxWidth='md' className={classes.container}>
                <Button color="inherit" onClick={() => {handleDownload('労使協定書(第1版).pdf')}}>
                    <img src={pdf} className={classes.icon} alt="労使協定書(第1版).pdf" />
                    労使協定書(第1版).pdf
                </Button>
            </Container>
        </div>
    );
}